/* blocks */
.header {
  padding: 40px 80px; }

.header__wrapper {
  max-width: 1600px;
  margin: 0 auto;
  position: relative; }

.header__logo {
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2; }

.header__user-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center; }

.header__user-menu a {
  text-decoration: none;
  color: inherit;
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  margin-left: 35px; }

.header__user-cart svg {
  display: none; }

.header__navigation-buton {
  display: none; }

.header__navigation {
  position: relative;
  margin-top: 100px; }

.header__menu {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }

.header__menu a {
  text-decoration: none;
  color: inherit;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  margin-left: 35px; }

.header__menu-mobile {
  display: none; }

.header__login-mobile {
  display: none; }

.header__search {
  position: absolute;
  left: 0;
  top: -100px;
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap; }

.header__search-input {
  border: none; }
  .header__search-input::placeholder {
    font-size: 14px;
    line-height: 21px;
    font-weight: 500;
    color: #9b9997; }

.header__search-submit {
  margin-right: 15px;
  background-color: transparent;
  border: none; }

@media (max-width: 1023px) {
  .header {
    padding: unset;
    padding-top: 25px; }
  .header__user-cart svg {
    fill: transparent;
    stroke: black; }
  .header__navigation-buton svg {
    stroke: black; }
  .header.header--menu-opened {
    background-color: #a87B62; }
  .header.header--menu-opened svg {
    fill: transparent;
    stroke: #ffffff; }
  .header__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row-reverse; }
  .header__user-login {
    display: none; }
  .header__user-cart {
    position: relative; }
  .header__user-cart span {
    display: none; }
  .header__user-cart svg {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .header__navigation {
    margin-top: unset; }
  .header__navigation-wrap {
    display: flex;
    flex-direction: column;
    position: relative;
    margin: unset;
    margin-top: -3px;
    padding-top: 50px;
    padding-bottom: 20px;
    z-index: -1; }
  .header__menu {
    flex-direction: column;
    align-items: flex-start; }
  .header__menu li {
    margin-bottom: 20px; }
  .header__menu a {
    font-size: 24px;
    line-height: 36px;
    font-weight: 500;
    margin-left: unset; }
  .header__menu-mobile {
    list-style: none;
    margin: 0;
    padding: 0;
    display: block;
    margin-top: 20px; }
  .header__menu-mobile li {
    margin-bottom: 15px; }
  .header__login-mobile {
    text-decoration: none;
    color: inherit;
    display: block;
    margin-top: 30px; }
  .header__menu-mobile a {
    text-decoration: none;
    color: inherit;
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
    opacity: 0.75; }
  .header__search {
    display: block;
    position: relative;
    left: unset;
    top: unset;
    order: -1;
    z-index: 2;
    margin: 30px 0; }
  .header__search-input {
    opacity: 0.15;
    width: 88%;
    height: 40px;
    border-radius: 21px;
    padding-left: 30px; }
    .header__search-input::placeholder {
      opacity: 0; }
  .header__search-submit {
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%); }
  .header__search-submit svg {
    fill: white; }
  .header__navigation-buton {
    display: block;
    background-color: transparent;
    border: none; }
  .header__navigation--js .header__navigation-wrap {
    display: none; }
  .header__navigation--menu-opened {
    position: absolute;
    width: 100vw;
    z-index: 1; }
  .header__navigation--menu-opened .header__navigation-wrap {
    display: flex;
    width: 100%;
    background-color: #a87B62;
    padding-left: 30px; }
  .header__navigation--menu-opened a {
    color: #ffffff; } }

.footer {
  background-color: black;
  color: #ffffff; }

.footer a {
  text-decoration: none;
  color: inherit;
  font-size: 16px;
  line-height: 130%;
  font-weight: 500; }

.footer ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.footer__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1600px;
  margin: 0 auto;
  padding: 100px 5%; }

.footer__title {
  font-family: "Cormorant", "Times New Roman", serif;
  font-style: normal;
  font-weight: 600;
  font-size: 34px;
  line-height: 130%;
  letter-spacing: 0.03em;
  margin: 0; }

.footer__description {
  line-height: 130%;
  color: #9b9997;
  margin-top: 10px; }

.footer__subscribe {
  position: relative; }

.footer__subscribe input {
  width: 100%;
  padding: 10px 0;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #ffffff; }
  .footer__subscribe input::placeholder {
    font-size: 14px;
    line-height: 21px;
    font-weight: 500;
    color: #9b9997; }

.footer__subscribe button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  color: #ffffff; }

.footer__subscribe svg {
  stroke: white; }

.footer__wrapper {
  margin-right: 30px;
  margin-bottom: 30px; }

.footer__navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }

.footer__navigation li {
  margin-bottom: 30px; }
  .footer__navigation li:last-of-type {
    margin-bottom: unset; }

.footer__navigation ul {
  margin-right: 80px; }

.footer__social li {
  position: relative;
  padding-left: 20px; }

.footer__social svg {
  position: absolute;
  left: 0;
  top: 2px; }

.footer__social li:first-of-type svg {
  left: 5px; }

@media (max-width: 1023px) {
  .footer__inner {
    display: block; } }

@media (max-width: 767px) {
  .footer__navigation {
    display: block; }
  .footer__navigation ul {
    margin: 0;
    margin-bottom: 50px; }
  .footer__social li {
    padding: 0;
    margin-right: 20px;
    margin-bottom: unset; }
  .footer__social {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center; }
  .footer__social svg {
    position: static; }
  .footer__social span {
    display: none; } }
